.theme-f {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 25px;
}

.ml-2 {
  margin-left: 10px !important;
}

.p-1 {
  padding: 0.5rem !important;
}

.p-2 {
  padding: 1rem !important;
}

.p-3 {
  padding: 1.5rem !important;
}

.p-4 {
  padding: 2rem !important;
}

.cursor-pointer {
  cursor: pointer;
}

.colordiv.active {
  border: 2px solid #000;
  box-shadow: 0 0 10px #719ece;
}

.colordiv:hover {
  border: 2px solid #000;
  box-shadow: 0 0 10px #719ece;
}

.default {
  background: #da3768;
}

.commCircle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: auto;
}

.color-1 {
  background: #055cdf;
}

.color-2 {
  background: #09eb8d;
}

.color-3 {
  background: #03d3f8;
}
